.breadcrumbs {
  width: 100%;
  height: 40px;
  border-bottom: 1px solid #e2e2e2;
  line-height: 40px;
}
.breadcrumbs .breadcrumb {
  height: 41px;
}
.breadcrumbs .breadcrumb li {
  float: left;
  list-style: none;
}
.page-header {
  margin: 0 0 12px;
  border-bottom: 1px dotted #e2e2e2;
  padding-bottom: 16px;
  padding-top: 7px;
  text-align: center;
  padding: 10px;
}
